import { v4 as uuidv4 } from 'uuid'
import axios from 'axios'
import './pacContent'
import {genPac} from "./pacContent";

// 
let greenhubExt = {
  version: '',
  uid: '',
  connection: null,
  sharedDate: null,
  popupTime: null
}

let totalConnectionCount = '...'

// 
async function afterInstall() {
  //  id
  const uid = uuidv4()
  const manifestData = browser.runtime.getManifest()
  //  id 
  greenhubExt.version = manifestData.version
  greenhubExt.uid = uid
  await browser.storage.local.set({
    greenhubExt: greenhubExt
  })
}

browser.runtime.onInstalled.addListener(() => {
  afterInstall().then(() => {
    initGreenhub().then()
  })
  console.debug('installed...')
})


// 
async function initGreenhub() {
  //  id, , 
  let rst = await browser.storage.local.get('greenhubExt')
  greenhubExt = {...greenhubExt, ...rst.greenhubExt}
  // 
  greenhubExt.sharedDate = greenhubExt.sharedDate ? new Date(greenhubExt.sharedDate) : null
  greenhubExt.popupTime = greenhubExt.popupTime ? new Date(greenhubExt.popupTime) : null
  // 
  if (greenhubExt.connection) {
    disconnect().then()
  }
}
initGreenhub().then()

// 
function onAuth(details, callback) {
  // todo:  details  challenger.host challenger.port  callback
  if (details.isProxy) {
    callback({
      authCredentials: {
        username: greenhubExt.connection.auth.username,
        password: greenhubExt.connection.auth.password
      }
    })
  }
}

// 
function setProxy(proxy) {
  const config = {
    /*
    mode: "fixed_servers",
    // todo: 
    rules: {
      singleProxy: {
        host: proxy.hostWithoutScheme,
        port: proxy.port,
        scheme: 'https'
      }
    }
     */
    mode: "pac_script",
    pacScript: {
      data: genPac(proxy.hostWithoutScheme, proxy.port)
    }
  }
  chrome.webRequest.onAuthRequired.addListener(
    onAuth,
    { urls: ["<all_urls>"] },
    ['asyncBlocking']
  )
  chrome.proxy.settings.set({
    value: config,
    scope: 'regular'
  }, () => {})
}

// 
function unsetProxy() {
  chrome.proxy.settings.set({
    value: {
      mode: 'system'
    },
    scope: 'regular'
  }, () => {})
  chrome.webRequest.onAuthRequired.removeListener(onAuth)
}

// 
async function connect(data) {
  console.debug('connecting..', data)
  // 
  if (greenhubExt.connection) {
    if (data.ip === greenhubExt.connection.ip) {
      return false
    }
    await disconnect()
  }
  let {
    // eslint-disable-next-line no-unused-vars
    ip,
    host,
    capacity,
    current
  } = data
  // 
  if (current >= capacity) {
    return false
  }
  // 
  let rst = await axios.post(`${host}/connect`, {
    f: greenhubExt.uid,
    v: greenhubExt.version
  })
  if (rst.status !== 200) {
    return false
  }
  console.debug('connecting to proxy: ', rst)
  const resp = rst.data
  // 
  greenhubExt.connection = {
    ...data,
    hostWithoutScheme: resp.host,
    auth: resp.auth,
    port: resp.port,
    timestamp: new Date().getTime()
  }
  browser.storage.local.set({
    greenhubExt: greenhubExt
  })
  // 
  setProxy(greenhubExt.connection)
  //  30 
  browser.alarms.create('disconnect', {
    delayInMinutes: 30
  })
  // 
  browser.browserAction.setIcon({
    path: {
      38: 'icons/38.png',
      48: 'icons/48.png',
      128: 'icons/128.png'
    }
  })
  return greenhubExt.connection
}

// 
async function disconnect() {
  // 
  unsetProxy()
  // 
  axios.post(`${greenhubExt.connection.host}/disconnect`, {
    f: greenhubExt.uid,
    v: greenhubExt.version
  }).then(resp => {
    if (resp.status !== 200) {
      console.debug('disconnect failed')
    }
  })
  // 
  greenhubExt.connection = null
  // 
  await browser.storage.local.set({
    greenhubExt: greenhubExt
  })
  // 
  browser.browserAction.setIcon({
    path: {
      32: 'icons/disabled32.png'
    }
  })
  //  (option, popup)
  browser.runtime.sendMessage({
    action: 'disconnected',
    data: {}
  })
}

// 
browser.alarms.onAlarm.addListener(alarm => {
  if (alarm.name === 'disconnect') {
    disconnect().then()
    browser.alarms.clearAll()
  }
})

// 
function totalConnections() {
  return Promise.resolve(totalConnectionCount)
}

// ,  option 
function setTotalConnections(data) {
  totalConnectionCount = data
  return totalConnections()
}

// 
async function userShared() {
  try {
    const now = new Date()
    greenhubExt.sharedDate = now
    await browser.storage.local.set({
      greenhubExt: {
        ...greenhubExt,
        sharedDate: now.getTime()
      }
    })
    return true
  } catch (_) {
    return false
  }
}

// 
async function sharePopupPopped() {
  try {
    const now = new Date()
    greenhubExt.popupTime = now
    await browser.storage.local.set({
      greenhubExt: {
        ...greenhubExt,
        popupTime: now.getTime()
      }
    })
    return true
  } catch (_) {
    return false
  }
}

browser.runtime.onMessage.addListener(msg => {
  if (typeof msg === "object" && 'action' in msg) {
    switch (msg.action) {
      case 'extInfo':
        return Promise.resolve(greenhubExt)
      case 'connect':
        return connect(msg.data)
      case 'disconnect':
        return disconnect()
      case 'getTotalConnections':
        return totalConnections()
      case 'setTotalConnections':
        return setTotalConnections(msg.data)
      case 'userShared':
        return userShared()
      case 'sharePopupPopped':
        return sharePopupPopped()
    }
  }
})
